.transactionHistory {
  width: 500px;
  text-align: center;
  border: 2px solid gray;
  font-size: 15px;
}
thead tr {
  background-color: lightblue;
}

th,
td {
  border-style: solid;
  border-color: white;
}

tbody tr:nth-child(odd) {
  background-color: rgb(255, 255, 255);
}

tbody tr:nth-child(even) {
  background-color: rgb(223, 219, 219);
}
